import { Service } from '../utils/service.js';
export function createStop({network, preload, ipns, repo, mfsPreload}) {
  const stop = async () => {
    await Promise.all([
      preload.stop(),
      ipns.stop(),
      mfsPreload.stop()
    ]);
    await Service.stop(network);
    await repo.close();
  };
  return stop;
}