import { Service } from '../utils/service.js';
export function createStart({network, preload, peerId, keychain, repo, ipns, mfsPreload, print, hashers, options}) {
  const start = async () => {
    const {libp2p} = await Service.start(network, {
      peerId,
      repo,
      print,
      hashers,
      options
    });
    await Promise.all([
      ipns.startOnline({
        keychain,
        libp2p,
        peerId,
        repo
      }),
      preload.start(),
      mfsPreload.start()
    ]);
  };
  return start;
}